export const CustomMDXRender = (preProps) => {
  console.log(preProps)
  return (
    <StyleMarkdown id="stylemd">
      <MDXProvider components={CodeComponents}>
        <MDXRenderer {...preProps} />
      </MDXProvider>
    </StyleMarkdown>
  )
}


